import React from "react";

import EasySocialImg from "../assets/easysocial.svg";

const Intentions = ({ isMobile, className }) => {
  return (
    <section className={className}>
      <div className="content">
        <div className="data">
          <p className="info">
            {
              "EasySocial intends to simplify digital marketing for all sizes of businesses by intelligently  automating redundant tasks. Hence enabling small and medium businesses to focus more on operational challenges and large enterprises to declutter and laser focus on identifying customers with high purchase intent."
            }
          </p>
        </div>
        <div className="image">
          <img src={EasySocialImg} alt="Easy Social" className="block-image" />
        </div>
      </div>
    </section>
  );
};

export default Intentions;
